<template>
  <div>
    <todo-header @input="input"></todo-header>
    <todo-main :dataList="dataList" @del="del" @changeStatus="changeStatus"></todo-main>
    <todo-footer :residue="residue"></todo-footer>
  </div>
</template>
<script>
import todoFooter  from './todoFooter/index.vue'
import todoMain from './todoMain/index.vue'
import todoHeader from './todoHeader/index.vue'
export default {
  name: 'todoCom',
  components: {
    'todo-footer': todoFooter,
    'todo-main': todoMain,
    'todo-header': todoHeader
  },
  data () {
    return {
        dataList: JSON.parse(localStorage.getItem('todoList')) || []
    }
  },
  computed: {
    residue() {
      return this.dataList.filter(item => item.status === 0).length
    }
  },
  methods: {
    input(data) {
      this.dataList.push({
        id: this.dataList.length > 0 ? this.dataList[this.dataList.length - 1].id + 1 : 1,
        content: data,
        status: 0
      })
      localStorage.setItem('todoList', JSON.stringify(this.dataList))
    },
    del(id) {
      this.dataList = this.dataList.filter(item => item.id !== id)
      localStorage.setItem('todoList', JSON.stringify(this.dataList))
    },
    changeStatus(id) {
      this.dataList = this.dataList.map(item => {
        if (item.id === id) {
          item.status = item.status === 0 ? 1 : 0
        }
        return item
      })
      localStorage.setItem('todoList', JSON.stringify(this.dataList))
    }
  }
}
</script>
