<template>
  <div>
    <h1>全选/反选</h1>
    <br>
    <hr>
    <br>
    <label for="select">全选：</label>  
    <input type="checkbox" v-model="isAllSelected" @click="selectAll" >
    <br>
    <div>
      <label v-for="item in list" :key="item">
        <input type="checkbox" v-model="selected" :value="item" >{{ item }}
      </label>
    </div>

    <p>全选：{{ isAllSelected }}</p>
    <p>已选择：{{ selected }}</p>

  </div>
</template>
<script>
export default {
  data() {
    return {
      list : ['猪八戒', '孙悟空', '沙僧', '唐僧'],
      selected: []
    }
  },
  computed: {
    // 计算属性，判断是否全选
    isAllSelected: {
      get() {
        return this.list.length === this.selected.length
      },
      set(value) {
        this.selected = value ? this.list : []
      }
    }
  },
  methods: {
    selectAll() {
      if (this.isAllSelected) {
        this.selected = []
      } else {
        this.selected = this.list
      }
    }
  }
}
</script>
