<template>
  <div>
    <input type="text" placeholder="请输入待办事项" @keyup.enter="enterFn">
  </div>
</template>
<script>
export default {
    name: 'todoHeader',
    methods: {
        enterFn (e) {
            if (!e.target.value) {
                return
            }
            this.$emit('input', e.target.value)
            e.target.value = ''
        }
    }
}
</script>
