<template>
  <div>
    <div class="box-container">
        <div class="left-area">
            <table>
                <tr>
                    <th>编号</th>
                    <th>科目</th>
                    <th>成绩</th>
                    <th>考试时间</th>
                    <th>操作</th>
                </tr>
                <tr v-for="(item, index) in dataList" :key="index">
                    <td>{{item.id}}</td>
                    <td>{{item.subject}}</td>
                    <td :style="{'color': item.score >= 60 ? 'green' : 'red'}">{{item.score}}</td>
                    <td>{{item.time}}</td>
                    <td><a @click.prevent="deleteRow(index)">删除</a></td>
                </tr>
                <tr>
                    <td colspan="5">
                        <span>总分：{{ totalScore }}</span>&nbsp;&nbsp;&nbsp;&nbsp;
                        <span>平均分：{{ average }}</span>
                    </td>
                </tr>

            </table>
        </div>
        <div class="right-area">
            <label for="kemu">科目</label>
            <input type="text" v-model.trim="subject" placeholder="请输入科目">
            <label for="kemu">分数</label>
            <input type="text" v-model.number="score" placeholder="请输入分数">
            <button @click="add">添加</button>
        </div>
    </div>
  </div>
</template>
<script>
export default {
    name: 'tableList',
    data() {
        return {
            subject: '',
            score: 0,
            dataList: JSON.parse(localStorage.getItem('tablelist')) || [],
        }
    },
    computed: {
        totalScore() {
            return this.dataList.reduce((total, item) => total + item.score, 0)
        },
        average() {
            return isNaN(this.totalScore / this.dataList.length) ? 0 : (this.totalScore / this.dataList.length).toFixed(2) 
        }
    },

    methods: {
        add() {
            if(this.subject && this.score) {
                this.dataList.push({
                    id: this.dataList.length + 1,
                    subject: this.subject,
                    score: this.score,
                    time: new Date().toLocaleString()
                })
                this.subject = ''
                this.score = 0
                localStorage.setItem('tablelist', JSON.stringify(this.dataList))

            } else {
                alert('请输入科目和分数')
            }
        },
        deleteRow(index) {
            this.dataList.splice(index, 1)
            localStorage.setItem('tablelist', JSON.stringify(this.dataList))
        }
    }

 }
    

</script>
